import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
function load (dir,component) {
  return () => import(`@/components/${dir}/${component}.vue`)
}

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/render',
      name: 'render',
      component: load('render','Parent')
    },
    {
      path: '/vnode',
      name: 'vnode',
      component: load('vnode','Parent')
    },

    {
      path: '/snabbdom',
      name: 'snabbdom',
      component: load('snab','Snab')
    },
    {
      path: '/s',
      name: 's',
      component: () => import(`@/components/Vnode.vue`)
    },
    {
      path: '*',
      redirect: {name:'snabbdom'}
    }
  ]
})



